import React from "react";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";

import useMountedRef from "../hooks/useMounterRef";
import { getMovieRank } from "../slices/MovieRankSlice";

//  
import Spinner from "../components/Spinner";
//  CSS  
import Error from "../components/Error";
//    
import Table from "../components/Table";
//    
import BarChartView from "../components/BarChartView";
//   
import dayjs from "dayjs";

//     flex-box
const Container = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  .flex-item {
    flex-basis: 50%;
    box-sizing: border-box;
    padding: 10px;
  }
`;

const MoviRank = () => {
  const dispatch = useDispatch();
  // Redux Store Ajax  
  const { data, loading, error } = useSelector((state) => state.movieRank);
  //       
  const [targetDt, setTargetDt] = React.useState(
    dayjs().add(-1, "d").format("YYYY-MM-DD")
  );

  //        hook
  const mountedRef = useMountedRef();
  //    
  const [chartData, setchartData] = React.useState();

  //          --> Ajax 
  React.useEffect(() => {
    dispatch(getMovieRank({ targetDt: targetDt.replaceAll("-", "") }));
  }, [dispatch, targetDt]);

  //     
  const onDateChange = React.useCallback(
    (e) => {
      e.preventDefault();
      //    --> React.useEffect()   .
      setTargetDt(e.target.value);
    },
    [setTargetDt]
  );

  // Ajax       chartData  .
  // Ajax     ,        .
  React.useEffect(() => {
    //       .
    // mountedRef.current true  
    if (mountedRef.current) {
      const newData = {
        movieNm: [],
        audiCnt: [],
      };

      data.boxOfficeResult.dailyBoxOfficeList.forEach((v, i) => {
        newData.movieNm.push(v.movieNm);
        newData.audiCnt.push(v.audiCnt);
      });
      setchartData(newData);
    }
  }, [mountedRef, data]);

  return (
    <div>
      <Spinner visible={loading} />
      <form>
        <input
          type="date"
          className="form-control"
          placeholder=" "
          value={targetDt}
          onChange={onDateChange}
        />
      </form>

      <hr />

      {error ? (
        <Error error={error} />
      ) : (
        <Container>
          <div className="flex-item">
            <BarChartView chartData={chartData} />
          </div>
          <div className="flex-item">
            <Table>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th> </th>
                  <th></th>
                  <th>  </th>
                  <th> </th>
                </tr>
              </thead>
              <tbody>
                {data &&
                  data.boxOfficeResult.dailyBoxOfficeList.map((v, i) => {
                    return (
                      <tr key={i}>
                        <td>{v.rank}</td>
                        <td>{v.movieNm}</td>
                        <td>{Number(v.audiCnt).toLocaleString()}</td>
                        <td>{Number(v.salesAmt).toLocaleString()}</td>
                        <td>{Number(v.audiAcc).toLocaleString()}</td>
                        <td>{Number(v.salesAcc).toLocaleString()}</td>
                      </tr>
                    );
                  })}
              </tbody>
            </Table>
          </div>
        </Container>
      )}
    </div>
  );
};

export default React.memo(MoviRank);
